(function() {
  'use strict';

  angular
    .module('tickTock')
    .directive('tickTockNavbar', tickTockNavbar);

  /** @ngInject */
  function tickTockNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          clockFormat: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
      
    };

    return directive;

    /** @ngInject */
    function NavbarController($scope, $state) {
      var vm = this;
      vm.navCollapsed = true;
      vm.isActive = true;
      vm.clockFormats = { 0:{ format: '12' }, 1:{ format: '24' } };
      

      vm.anaClo = function() {
        vm.toggleMenu();
        goTo('analogClock');
      };

      vm.digClo = function() {
        vm.toggleMenu();
        goTo('digitalClock');
      };

      vm.toggleMenu = function() {
        vm.navCollapsed = !vm.navCollapsed;
      };

      vm.toggleClock = function() {
        vm.isActive = !vm.isActive;

        if (vm.isActive) {
          vm.clockFormat = vm.clockFormats[0];
        } else {
          vm.clockFormat = vm.clockFormats[1];
        }

        sendTypeClock(vm.clockFormat);
      };

      function init() {
        vm.clockFormat = vm.clockFormats[0];
        sendTypeClock(vm.clockFormat);
      }

      function goTo(stateName) {
        $state.go(stateName);
      }

      function sendTypeClock() {
        $scope.$root.$broadcast('CLOCK_FORMAT_CHOOSE', vm.clockFormat);
      }

      init();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('tickTock')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('tickTock')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('tickTock')
    .directive('tickTockAlarms', tickTockAlarms);

  /** @ngInject */
  function tickTockAlarms() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/alarms/alarms.html',
      scope: {
          alarmsDetails: '='
      },
      controller: AlarmsController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function AlarmsController($scope, moment) {
      var vm = this,
          elCounter = 0,
          panelActive = false;
          
      vm.alarmsSets = [obj()];
      

      function obj() {
        var t = {
          name: '',
          h: 0,
          m: 0,
          time: '',
          ring: 0,
          active: false,
          id: elCounter,
          repeat: {
            'm': false,
            't': false,
            'w': false,
            'r': false,
            'f': false,
            's': false,
            'u': false
          }
        };

        return t;
      };

      vm.activeAlarm = function(alarmActive) {
        alarmActive.active = !alarmActive.active;
        var mom = moment({ hour: alarmActive.h, minute: alarmActive.m});
        alarmActive.time = mom.format('HH:mm');
        $scope.$root.$broadcast('ALARMS_SET', vm.alarmsSets);
      }

      vm.showAlarmPanel = function() {
        panelActive = !panelActive;
        $scope.$root.$broadcast('PANNEL_TOGGLE', panelActive);
      }

      vm.deleteAlarm = function(alarmActive) {
        var index = vm.alarmsSets.indexOf(alarmActive);
        vm.alarmsSets.splice(index, 1);
      };  

      vm.addAlarm = function(alarmActive) {
        elCounter++;
        vm.alarmsSets[vm.alarmsSets.length] = obj();
      }; 
      

      vm.activeDate = function(day, alarmActive) {

        alarmActive.repeat[day] = !alarmActive.repeat[day];
      };
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('tickTock')
    .directive('a', function() {
      return {
        restrict: 'E',
        link: function(scope, elem, attrs) {
          if(attrs.ngClick || attrs.href === '' || attrs.href === '#'){
            elem.on('click', function(e){
              e.preventDefault();
            });
          }
        }
      };
    }) 
    .controller('MainController', MainController);


  function MainController($interval, $scope, moment) {
    var vm = this;
    vm.panelActive = false;
    vm.alarmsSets = null;
    vm.formatChoose = {
      template: '',
      format24: false
    };
    vm.hours = '';
    vm.mins = '';
    vm.secs = '';
    vm.aa = '';

    var beep = new Audio('http://mikevanrossum.nl/stuff/doorbell.mp3');

    // $scope.$watch(function() {
    //   return vm.clockFormat;
    // }, function(current, original) {
    //   try {
    //     // console.log('vm.title is now %s', current.format);

    //     switch (current.format) {
    //       case '12': 
    //         vm.formatChoose.template = 'hh:mm:ss a';
    //         vm.formatChoose.format24 = false;
    //       break;
    //       case '24':
    //         vm.formatChoose.template = 'HH:mm:ss';
    //         vm.formatChoose.format24 = true;
    //       break;
    //     }
        
    //   } catch(e) { }
    // });

// alarmsDetail

    $scope.$on('CLOCK_FORMAT_CHOOSE', function(e, data) {
      try {
        // console.log('vm.title is now %s', data.format);

        switch (data.format) {
          case '12': 
            vm.formatChoose.template = 'hh:mm:ss a';
            vm.formatChoose.format24 = false;
          break;
          case '24':
            vm.formatChoose.template = 'HH:mm:ss';
            vm.formatChoose.format24 = true;
          break;
        }
        
      } catch(e) { }
    });

    $scope.$on('PANNEL_TOGGLE', function(e, data) {
      try {
        // console.log(data);
        vm.panelActive = data;
      } catch(e) { }
    });

    $scope.$on('ALARMS_SET', function(e, data) {
      try {
        vm.alarmsSets = data;
      } catch(e) { }
    });

    var checkMatch = function(val) {
      for (var i = 0; i < vm.alarmsSets.length; i++) {
        if (vm.alarmsSets[i].active) {
          if (vm.alarmsSets[i].time === val) {
            console.log(vm.alarmsSets[i].name);
            vm.alarmsSets[i].ring++;
            setTimeout( function() { beep.play(); }, 1000 * vm.alarmsSets[i].ring );
            if (vm.alarmsSets[i].ring === 3) {
              vm.alarmsSets[i].ring = 0;
              vm.alarmsSets[i].active = 0;
              break;
            }
          }
        }
      }
    };

    var setAnalogClock = function () {
      var hours = moment().format('hh'),
          minute = moment().format('mm'),
          seconds = moment().format('ss'),
          hourRotate,
          minRotate,
          secRotate,
          hrotate,
          mrotate,
          srotate;

      if (!vm.formatChoose.format24)
        vm.aa = moment().format('A'); 
      else
        vm.aa = null; 
      
      // hours = ((hours > 12) ? hours - 12 : hours);
      if (minute === 0){
        minRotate = 0;
      }else{
        minRotate = minute*6;
      }
      if (seconds === 0){
        secRotate = 0;
      }else{
        secRotate = seconds*6;
      }
      if (hours === 12){
        hourRotate = 0;
      } else {
        hourRotate = (hours*30) + (minute/2);
      }

      hrotate = "rotate(" + hourRotate + "deg)";
      mrotate = "rotate(" + minRotate + "deg)";
      srotate = "rotate(" + secRotate + "deg)";

      vm.hours = {"-moz-transform": hrotate, "-webkit-transform" : hrotate};
      vm.mins = {"-moz-transform": mrotate, "-webkit-transform" : mrotate};
      vm.secs = {"-moz-transform": srotate, "-webkit-transform" : srotate};
    };

    var tickTickTock = function() {
      vm.clock = moment().format();
      setAnalogClock();
      if (vm.alarmsSets)
        checkMatch(moment().format('HH:mm'));
    };

    tickTickTock();
    $interval(tickTickTock, 1000);
  }

})();

